import { createSlice } from '@reduxjs/toolkit'

export const HideCard = createSlice({
    name:'hideCard',
    initialState:{
        value:''
    },
    reducers:{
        setHideCard: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setHideCard } = HideCard.actions

export default HideCard.reducer