import {Outlet, NavLink, useSearchParams, Link} from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import './App.css';
import imageHambMenu from './imgs/hamburguer-menu-image.png'
import unitMedicines from './components/Data'
import { setInputValue } from './features/InputValue';
import { setCtrlOutcomes } from './features/CtrlOutcomes';
import { setCtrlOutcomesElement } from './features/CtrlOutcomesElement';
import { setCtrlOutcomesElementBtn } from './features/CtrlOutcomesElementBtn';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader'
import { StylesProvider } from "@material-ui/core/styles";


function App() {
  
  const medicines=unitMedicines()
  let[searchParams,setSearchParams]=useSearchParams()

  const inputValue=useSelector((state)=>state.inputValue.value)
  const crtlOutcomes=useSelector((state)=>state.ctrlOutcomesElement.value)
  const ctrlOutcomesElement=useSelector((state)=>state.ctrlOutcomesElement.value)
  const ctrlOutcomesElementBtn=useSelector((state)=>state.ctrlOutcomesElementBtn.value)

  const dispatch=useDispatch()

  return (

    <>

      <div id="header" >
        <Link to='/'><p>sis-atendimento</p></Link>
        <img src={imageHambMenu} alt="menu" />
      </div>

      <div id="side-menu" onChange={()=>dispatch(setInputValue(document.getElementById('search__input').value))}>                                                
        <NavLink to="/orcamento">
          <Button variant="contained" children='Orçamento' color="primary" id='side-menu__btn'/>
          {/* <button type='button' id='side-menu__btn' onClick={()=>dispatch(setCtrlOutcomesElement(''))}>Orçamento</button> */}
        </NavLink>

        <div id="search">
          <form>                 
            <input id="search__input" value={searchParams.get("filter") || ""}
            onChange={event => {
              let filter = event.target.value; 
              if (filter) { 
              setSearchParams({ filter });
              } else {
              setSearchParams({});
              }
              dispatch(setCtrlOutcomes('outcomes--show'))
              dispatch(setCtrlOutcomesElement('outcomes__element--show'))
              if(event.target.value===''){
              dispatch(setCtrlOutcomesElement(''))
              dispatch(setCtrlOutcomes(''))
              }}
            }
            onKeyDown={(event)=>{
              if(event.code==='Enter'){
                document.getElementById('search__input').value=""
                dispatch(setCtrlOutcomesElement(''))
                dispatch(setCtrlOutcomes(''))
              }
            }}
            type="search" placeholder='Medicamento..' autoComplete='on'/>

            <Link to={`/${inputValue}`}> 
              <Button variant="contained" color='primary' type='submit' 
              onClick={()=>{
                document.getElementById('search__input').value=""
                dispatch(setCtrlOutcomesElement(''))
                dispatch(setCtrlOutcomes(''))
              }}>
                Go!
              </Button>
            </Link>
          </form>               
        </div>   
        
        <StylesProvider injectFirst>
        <Card raised color="primary" className={'outcomes ' + crtlOutcomes} >
          <CardHeader className='outcomes-header' title="RESULTADOS:"/>
          
          {medicines

            .filter(invoice => {            
              let filter = searchParams.get("filter"); 
              if (!filter) return true; 
              let name = invoice.name.toLowerCase(); 
              return name.startsWith(filter.toLowerCase()); 
              }
            )

            .map(invoice => ( 

              <>
                <Link to={`/${ctrlOutcomesElementBtn}`}>
                  
                  <Button variant='contained' color='primary' id='outcomes__element-id' key={invoice.id} className={'outcomes__element '+ ctrlOutcomesElement}
                  onMouseOver={()=>dispatch(setCtrlOutcomesElementBtn(invoice.name))} onClick={()=>{                                                  
                    document.getElementById('search__input').value=invoice.name
                    dispatch(setCtrlOutcomesElement(''))
                    dispatch(setCtrlOutcomes('')) 
                    }
                  }>
                    {invoice.name}
                  </Button>
                  
                  
                </Link>
              </>)           
            ) 
          }
        
        </Card>
        </StylesProvider>

        <Outlet/>

      </div>
      
    </>
  )
}

export default App;
