import { createSlice } from '@reduxjs/toolkit'

export const PaymentSituationBtnBG = createSlice({
    name:'paymentSituationBtnBG',
    initialState:{
        value:'budget__situation-btn'
    },
    reducers:{
        setPaymentSituationBtnBG: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setPaymentSituationBtnBG } = PaymentSituationBtnBG.actions

export default PaymentSituationBtnBG.reducer