import { createSlice } from '@reduxjs/toolkit'

export const DoseValue = createSlice({
    name:'doseValue',
    initialState:{
        value:0
    },
    reducers:{
        setDoseValue: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setDoseValue } = DoseValue.actions

export default DoseValue.reducer