import Button from '@material-ui/core/Button';
import JsPDF from 'jspdf';
import { setPaymentSituation } from '../features/PaymentSituation';
import { setPaymentSituationBtnBG } from '../features/PaymentSituationBtnBG';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';
import { useEffect } from 'react';


export default function Board(){

  useEffect(()=>{
    moment.locale('pt-br')
    })

  const generatePDF = () => {
    const report = new JsPDF('portrait','pt','a4');
    report.html(document.querySelector('#report')).then(() => {
        report.save('orcamento.pdf');
    });
  }
  
  const currentYear = moment().format('L');

  const paymentSituation=useSelector((state)=>state.paymentSituation.value)
  const paymentSituationBtnBG=useSelector((state)=>state.paymentSituationBtnBG.value)

  const dispatch=useDispatch()

    return(
         
      <div className={"budget__chart " + localStorage.getItem('showChart') }>
        <div id='report'>
        <h2>Vet. Bruna de Lira Nepomuceno CRMV 4250</h2>
        <h3 style={{'padding':'0 0 0 0'}}>Orçamento:</h3>
          
          <table border='1' style={{borderCollapse:'collapse'}}>
            <thead>

              <tr>
                <th>Serviço/ Procedimento</th>
                <th>Quantidade</th>
                <th>R$</th>
              </tr>  

            </thead>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr1')}>

              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line1')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line2')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line3')}</th>
              </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr2')}>
              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line4')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line5')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line6')}</th>
              </tr>
            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr3')}>

              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line7')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line8')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line9')}</th>
              </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr4')}>
            
              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line10')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line11')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line12')}</th>
              </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr5')}>
            
              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line13')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line14')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line15')}</th>
              </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr6')}>

              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line16')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line17')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line18')}</th>
              </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr7')}>

              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line19')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line20')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line21')}</th>
              </tr>

            </tbody>

           <tbody className={'budget__element--hidden ' + localStorage.getItem('tr8')}>

              <tr>
                <th className='budget__element--light'>{localStorage.getItem('line22')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line23')}</th>
                <th className='budget__element--light'>{localStorage.getItem('line24')}</th>
              </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr9')}>

                <tr>
                  <th className='budget__element--light'>{localStorage.getItem('line25')}</th>
                  <th className='budget__element--light'>{localStorage.getItem('line26')}</th>
                  <th className='budget__element--light'>{localStorage.getItem('line27')}</th>
                </tr>

            </tbody>

            <tbody className={'budget__element--hidden ' + localStorage.getItem('tr10')}>

                <tr>
                  <th className='budget__element--light'>{localStorage.getItem('line28')}</th>
                  <th className='budget__element--light'>{localStorage.getItem('line29')}</th>
                  <th className='budget__element--light'>{localStorage.getItem('line30')}</th>                                                              
                </tr>

            </tbody>

            <tbody id='totalBudget'>

              <tr>
                <th><b>Valor Total:</b></th>
                <th>--</th>
                <th>R$ {localStorage.getItem('totalValue')}</th>
              </tr>
     
            </tbody>

          </table>
          

          <div id='budget__payment-div'>
            <h4>Status do Orçamento:</h4>
            <button type='text' className={paymentSituationBtnBG} onClick={()=>{
              if(paymentSituation==='Pendente'){
                dispatch(setPaymentSituation('Pago'))
                dispatch(setPaymentSituationBtnBG('budget__situation-btn--green'))
                console.log(currentYear)
              }else{
                dispatch(setPaymentSituation('Pendente'))
                dispatch(setPaymentSituationBtnBG('budget__situation-btn'))
                }
            }} >{paymentSituation}</button>
            
          </div>
          <p>{currentYear}</p>
          <hr/>

          </div>
          <Button onClick={generatePDF} variant='contained' style={{margin:'10px 0 10px 20px'}} className='budget__btn' type="button">Export PDF</Button>
          <Button variant='contained' type='button' style={{margin:'10px 0 10px 20px'}} className='budget__btn' onClick={()=>{
            localStorage.clear()
            window.location.reload()
          }}>
            Orçamento finalizado
          </Button>
        </div>


      )
}