import { createSlice } from '@reduxjs/toolkit'

export const PaymentSituation = createSlice({
    name:'paymentSituation',
    initialState:{
        value:'Pendente'
    },
    reducers:{
        setPaymentSituation: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setPaymentSituation } = PaymentSituation.actions

export default PaymentSituation.reducer