import { useParams } from "react-router-dom"; // Objetivo: habilitar uso de infos contidas na URL.
import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import unitMedicine from "./Data.js"
import { setMedCount } from "../features/MedCount.js";
import { setHideCard } from "../features/HideCard.js";
import { setHideNotFound } from "../features/HideNotFound.js";
import { Card } from "@material-ui/core";


export default function Medicine() {
  
  let medicines = unitMedicine()
  const params = useParams();

  const medCount=useSelector((state)=>state.medCount.value)
  const hideCard=useSelector((state)=>state.hideCard.value)
  const hideNotFound=useSelector((state)=>state.hideNotFound.value)

  const dispatch=useDispatch()
  
  useEffect(()=>{
    dispatch(setHideCard('card--hidden'))
    dispatch(setHideNotFound(' '))
    for(let medicine of medicines){
      if(medicine.name.toLowerCase()===params.medicineId.toLowerCase()){
        dispatch(setMedCount(medicine.id)) 
        dispatch(setHideCard(''))
        dispatch(setHideNotFound('card--hidden'))     
      }        
    }      
  })

  return(
    <>

      <Card className={"card " + hideCard}>
        <p key={medicines.id} ><strong>Nome do produto/serviço:</strong> {medicines[medCount].name}</p>
        <p key={medicines.id}> <strong>Tipo:</strong> {medicines[medCount].type}</p>
        <p key={medicines.id}><strong>Preço do serviço ou valor/ml: </strong>  R$ {medicines[medCount].price}</p>
        <p key={medicines.id}><strong>Id:</strong> {medicines[medCount].id}</p>
      </Card>

      <Card className={"card__not-found " + hideNotFound}>
        <h2>Não encontrado!</h2>
      </Card>

    </>
  )
}