import { createSlice } from '@reduxjs/toolkit'

export const CtrlOutcomes = createSlice({
    name:'ctrlOutcomes',
    initialState:{
        value:'outcomes--show'
    },
    reducers:{
        setCtrlOutcomes: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setCtrlOutcomes } = CtrlOutcomes.actions

export default CtrlOutcomes.reducer