import { createSlice } from '@reduxjs/toolkit'

export const DosePrice = createSlice({
    name:'dosePrice',
    initialState:{
        value:0
    },
    reducers:{
        setDosePrice: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setDosePrice } = DosePrice.actions

export default DosePrice.reducer