import React from 'react'
import unitMedicines from './Data.js'
import { useSelector, useDispatch } from 'react-redux'
import { setDoseValue } from '../features/DoseValue.js'
import { setDosePrice } from '../features/DosePrice.js'
import { setMedName } from '../features/MedName.js'
import Board from './Board.js'
import Card from '@material-ui/core/Card'
import { Button } from '@material-ui/core'


export default function Consultar(){

  const medicines=unitMedicines()

  const doseValue=useSelector((state)=>state.doseValue.value)
  const dosePrice=useSelector((state)=>state.dosePrice.value)
  const medName=useSelector((state)=>state.medName.value)
  const sum=useSelector((state)=>state.sum.value)
 
  const dispatch=useDispatch()
   
  const generateItem = () =>{
    if(!localStorage.getItem('count') && !localStorage.getItem('rowCount')){ //in case of there is no meds added.
      localStorage.setItem('showChart', 'budget__chart--show')
      localStorage.setItem('count', 1 )
      localStorage.setItem('rowCount',1)
      localStorage.setItem('line'+localStorage.getItem('count'),medName)
      localStorage.setItem('count', parseFloat(localStorage.getItem('count')) + 1 )
      localStorage.setItem('line'+localStorage.getItem('count'),doseValue)
      localStorage.setItem('count', parseFloat(localStorage.getItem('count')) + 1 )
      localStorage.setItem('line'+localStorage.getItem('count'),dosePrice)
      localStorage.setItem('total', localStorage.getItem('line'+localStorage.getItem('count')))
      localStorage.setItem('totalValue',parseFloat(localStorage.getItem('total')+localStorage.getItem('totalValue')))
      // dispatch (setSum(localStorage.getItem('total')))
      localStorage.setItem('count', parseFloat(localStorage.getItem('count')) + 1 )
      localStorage.setItem('tr' + localStorage.getItem('rowCount'), 'budget__element--show')
      localStorage.setItem('rowCount', parseFloat(localStorage.getItem('rowCount')) + 1 )
      localStorage.setItem('paymentSituation', 'Pendente')
      localStorage.setItem('paymentBtnBG', 'budget__situation-btn')
    }else{ //in case there is at least one med added.
      localStorage.setItem('showChart', 'budget__chart--show')
      localStorage.setItem('line'+localStorage.getItem('count'),medName)
      localStorage.setItem('count', parseFloat(localStorage.getItem('count')) + 1 )
      localStorage.setItem('line'+localStorage.getItem('count'),doseValue)
      localStorage.setItem('count', parseFloat(localStorage.getItem('count')) + 1 )
      localStorage.setItem('line'+localStorage.getItem('count'),dosePrice)
      localStorage.setItem('total', parseFloat(localStorage.getItem('line'+localStorage.getItem('count'))) +  + sum)
      localStorage.setItem('totalValue', parseFloat(localStorage.getItem('total')) + parseFloat(localStorage.getItem('totalValue')))
      // dispatch (setSum(localStorage.getItem('total')))
      localStorage.setItem('count', parseFloat(localStorage.getItem('count')) + 1 )
      localStorage.setItem('tr' + localStorage.getItem('rowCount'), 'budget__element--show')
      localStorage.setItem('rowCount', parseFloat(localStorage.getItem('rowCount')) + 1 )
    }
    document.getElementById('dose').value=0
    document.getElementById('medName').value='defaultValue'
    dispatch(setDosePrice(0))  
  }

  return(
    <>
      <Card id="budget__element">

        <label>Nome do produto:</label>
        <select id="medName" onChange={(e)=>{ 
          dispatch(setMedName(e.target.value))
          const medicine = {price:medicines.find(i => i.name === e.target.value).price}
          dispatch( setDosePrice(doseValue*medicine.price < 7 ? 7 : doseValue*medicine.price) )
          //dispatch(setDosePrice(0))   
          //document.getElementById('dose').value=0                                       
        }} name="f_produtc" className="breakLine">

          <option value="defaultValue">Selecione um item:</option>
            {medicines.map((medicine)=>{
              if(medicine.priceable){
                return (                                     
                  <option value={medicine.name} key={medicine.id}>
                    {medicine.name}
                  </option>
                )
              }                                    
            }
          )}
        </select><br/> 

        <label className="breakLine">Quantidade:</label>
        <input id="dose" onKeyDown={(event)=>{
          if(event.code==='Enter'){
            generateItem()                                                                                        
          }}}
          value={doseValue}
        type="number" onChange={ (event)=> {
          dispatch(setDoseValue(event.target.value))
          const medicine = {price:medicines.find(i => i.name === medName).price}
          dispatch( setDosePrice(event.target.value*medicine.price < 7 ? 7 : event.target.value*medicine.price) ) 
          
        }} name="f_dose"
        step="0.1" min="0" max="10" required/><br/>

        <label>R$:</label>
        <input id="doseValue" type="text" name="f_valorDaDose" size="5" value={dosePrice < 7 ? 7 : dosePrice} readOnly/>

        <Button type="submit" variant='contained' onClick={generateItem} id="budget__add"> + </Button>

      </Card>

      <Board/>
    </> 
            
  )
}