import { createSlice } from '@reduxjs/toolkit'

export const MedCount = createSlice({
    name:'medCount',
    initialState:{
        value:0
    },
    reducers:{
        setMedCount: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setMedCount } = MedCount.actions

export default MedCount.reducer