import { configureStore } from '@reduxjs/toolkit'
import DoseValue from './features/DoseValue'
import DosePrice from './features/DosePrice'
import MedName from './features/MedName'
import Sum from './features/Sum'
import InputValue from './features/InputValue'
import CtrlOutcomes from './features/CtrlOutcomes'
import CtrlOutcomesElement from './features/CtrlOutcomesElement'
import CtrlOutcomesElementBtn from './features/CtrlOutcomesElementBtn'
import MedCount from './features/MedCount'
import HideCard from './features/HideCard'
import HideNotFound from './features/HideNotFound'
import PaymentSituation from './features/PaymentSituation'
import PaymentSituationBtnBG from './features/PaymentSituationBtnBG'

export default configureStore({
  reducer: {
    doseValue:DoseValue,
    dosePrice:DosePrice,
    medName:MedName,
    sum: Sum,
    inputValue:InputValue,
    ctrlOutcomes:CtrlOutcomes,
    ctrlOutcomesElement:CtrlOutcomesElement,
    ctrlOutcomesElementBtn:CtrlOutcomesElementBtn,
    medCount:MedCount,
    hideCard:HideCard,
    hideNotFound:HideNotFound,
    paymentSituation:PaymentSituation,
    paymentSituationBtnBG:PaymentSituationBtnBG
  },
})