import { createSlice } from '@reduxjs/toolkit'

export const MedName = createSlice({
    name:'medName',
    initialState:{
        value:''
    },
    reducers:{
        setMedName: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setMedName } = MedName.actions

export default MedName.reducer