import { createSlice } from '@reduxjs/toolkit'

export const HideNotFound = createSlice({
    name:'hideNotFound',
    initialState:{
        value:''
    },
    reducers:{
        setHideNotFound: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setHideNotFound } = HideNotFound.actions

export default HideNotFound.reducer