import { createSlice } from '@reduxjs/toolkit'

export const CtrlOutcomesElement = createSlice({
    name:'ctrlOutcomesElement',
    initialState:{
        value:0
    },
    reducers:{
        setCtrlOutcomesElement: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setCtrlOutcomesElement } = CtrlOutcomesElement.actions

export default CtrlOutcomesElement.reducer