import { createSlice } from '@reduxjs/toolkit'

export const CtrlOutcomesElementBtn = createSlice({
    name:'ctrlOutcomesElementBtn',
    initialState:{
        value:0
    },
    reducers:{
        setCtrlOutcomesElementBtn: (state,value)=>{
            state.value=value.payload
        },
    }
})

export const { setCtrlOutcomesElementBtn } = CtrlOutcomesElementBtn.actions

export default CtrlOutcomesElementBtn.reducer